import React, { useState } from "react";
import { Dropdown } from 'primereact/dropdown';

export default function Contact({ disableImage }) {

    const [selectedCity, setSelectedCity] = useState(null);
    const cities = [
        { name: 'New York', code: 'NY' },
        { name: 'Rome', code: 'RM' },
        { name: 'London', code: 'LDN' },
        { name: 'Istanbul', code: 'IST' },
        { name: 'Paris', code: 'PRS' }
    ];

    return (
        <section className='py-3 lg:py-10 bg-white'>

            <div className='container grid grid-cols-12 gap-5 lg:gap-8'>

                <form className='col-span-12 lg:col-span-6 !mb-10 lg:mb-0' onSubmit={e => e.preventDefault}>


                    <h1 className='mb-8 font-bold font-sans text-[30px]'>Contacto</h1>

                    <div className='flex items-center justify-between'>

                        <div className='w-[49%]'>

                            <label className='text-[#3F2621] font-medium font-sans'>Nombre</label>

                            <input type='text' className='p-3 border-b border-b-[#3F2621] w-full font-sans text-[13px] placeholder:text-[#3F2621]' placeholder='Su nombre' />

                        </div>

                        <div className='w-[49%]'>

                            <label className='text-[#3F2621] font-medium font-sans'>Apellido</label>

                            <input type='text' className='p-3 border-b border-b-[#3F2621] w-full font-sans text-[13px] placeholder:text-[#3F2621]' placeholder='apellido' />

                        </div>

                    </div>


                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium font-sans'>Empresa</label>

                        <input type='text' className='p-3 border-b border-b-[#3F2621] w-full font-sans text-[13px] placeholder:text-[#3F2621]' placeholder='Su nombre' />

                    </div>

                    <div className='my-10 form-dropdown'>

                        <label className='text-[#3F2621] font-medium font-sans'>Motivo del env√≠o</label>

                        <Dropdown value={selectedCity} onChange={(e) => setSelectedCity(e.value)} options={cities} optionLabel="name"
                            placeholder="Select a City" className="w-full md:w-14rem" />

                    </div>

                    <div className='my-10'>

                        <label className='text-[#3F2621] font-medium font-sans'>tu mensaje</label>

                        <textarea rows={4} className="block resize-none mt-3 w-full border border-[#3F2621] p-3 rounded-[5px]" placeholder="tipo.."></textarea>


                    </div>

                    <button className="p-4 w-full text-white text-center font-bold font-sans rounded-[30px] bg-[#45B8EA]">Enviar</button>


                </form>

                <div className={`col-span-12 lg:col-span-6 ${disableImage ? "hidden lg:flex" : "flex"} items-center justify-center p-5 -order-1 lg:order-[initial]`}>

                    <img src={require('./contacta.png')} className="w-full lg:w-[90%] h-full object-fill" />

                </div>

            </div>

        </section>
    )
}
